function generateLoot(lvl) {
  var loot = null;
  if (lvl <= 4) {
    loot = roll([
      [1, 30, roll('5d6') + ' CP'],
      [31, 60, roll('4d6') + ' SP'],
      [61, 70, roll('3d6') + ' EP'],
      [71, 95, roll('3d6') + ' GP'],
      [96, 100, roll('1d6') + ' PP']
    ]);
  }
  else if (lvl <= 10) {
    loot = roll([
      [1, 30, roll('4d6')*100 + ' CP ' + roll('1d6')*10 + ' EP'],
      [31, 60, roll('6d6')*10 + ' SP ' + roll('2d6')*10 + ' GP'],
      [61, 70, roll('3d6')*10 + ' EP ' + roll('2d6')*10 + ' GP'],
      [71, 95, roll('4d6')*10 + ' GP'],
      [96, 100, roll('2d6')*10 + ' GP ' + roll('3d6') + ' PP']
    ]);
  }
  else if (lvl <= 16) {
    loot = roll([
      [1, 20, roll('4d6')*100 + ' SP ' + roll('1d6')*100 + ' GP'],
      [21, 35, roll('1d6')*100 + ' EP ' + roll('1d6')*100 + ' GP'],
      [36, 75, roll('2d6')*100 + ' GP ' + roll('1d6')*10 + ' PP'],
      [76, 100, roll('2d6')*100 + ' GP ' + roll('2d6')*10 + ' PP']
    ]);
  }
  else {
    loot = roll([
      [1, 15, roll('2d6')*1000 + ' EP ' + roll('8d6')*100 + ' GP'],
      [16, 55, roll('1d6')*1000 + ' GP ' + roll('1d6')*100 + ' PP'],
      [56, 100, roll('1d6')*1000 + ' GP ' + roll('2d6')*100 + ' PP']
    ]);
  }
  return loot;
}

function generateGem(gp) {
  var gem = null;
  if (gp == 10) {
    gem = roll([
      'Azurite',
      'Banded agate',
      'Blue quartz',
      'Eye agate',
      'Hematite',
      'Lapis lazuli',
      'Malachite',
      'Moss agate',
      'Obsidian',
      'Rhodochrosite',
      'Tiger eye',
      'Turquoise'
    ]);
  }
  else if (gp == 50) {
    gem = roll([
      'Bloodstone',
      'Carnelian',
      'Chalcedony',
      'Chrysoprase',
      'Citrine',
      'Jasper',
      'Moonstone',
      'Onyx',
      'Quartz',
      'Sardonyx',
      'Star rose quartz',
      'Zircon'
    ]);
  }
  else if (gp == 100) {
    gem = roll([
      'Amber',
      'Amethyst',
      'Chrysoberyl',
      'Coral',
      'Garnet',
      'Jade',
      'Jet',
      'Pearl',
      'Spinel',
      'Tourmaline'
    ]);
  }
  else if (gp == 500) {
    gem = roll([
      'Alexandrite',
      'Aquamarine',
      'Black pearl',
      'Blue spinel',
      'Peridot',
      'Topaz'
    ]);
  }
  else if (gp == 1000) {
    gem = roll([
      'Black opal',
      'Blue sapphire',
      'Emerald',
      'Fire opal',
      'Opal',
      'Star ruby',
      'Star sapphire',
      'Yellow sapphire'
    ]);
  }
  else if (gp == 5000) {
    gem = roll([
      'Black sapphire',
      'Diamond',
      'Jacinth',
      'Ruby'
    ]);
  }
  return gem;
}

